body {
    background-color: #F3F7FA;
    min-width: 320px;
    width: auto !important;
    width: 320px;
}



.Sidebar {
    background: white;
    text-align: left;
}

.Sidebar span {
    font-size: 12px;
    color: #4A4A4A;
    letter-spacing: 0.5px;
    line-height: 19px;
}

.CurrencyFilter {
    border: 1px solid #D2D5D6;
    color: #298CEC;
    background-color: white;
}

.CurrencyFilter:active,
.activeButton {
    background: #2196F3;
    border: 1px solid #2196F3;
    color: white;
}

.CurrencyFilter:hover {
    background: #F2FCFF !important;
    color: #298CEC !important;
    border: 1px solid #64B5F5 !important;
    cursor: pointer;
}

.TransferFilter li {
    font-size: 13px;
    color: #4A4A4A;
    //line-height: 35px;
    border: none;
}

.TransferFilter li:hover {
    background: #F1FCFF;
    cursor: pointer;
}

.TransferFilter li>span {
    margin-left: 10px;
}

.Ticket {
    background: #FFFFFF;
}

.TicketInfo {
    border-right: 1px solid #ECEFF1;
}

.TicketSchedule {
    border-left: 1px solid #ECEFF1;
}

.TicketInfo button {
    background: #FF6D00;
   // box-shadow: 0 1px 0 0 #D64D08, 0 2px 1px 0 rgba(0,0,0,0.10);
   // border-radius: 5px;
   // font-size: 16px;
    color: white;
}

.TicketInfo button:hover {
    cursor: pointer;
    background: #FF8124;
    box-shadow: 0 1px 0 0 #F7661D, 0 1px 5px 0 rgba(0,0,0,0.25);
}

.FontTime {
    font-size: 32px;
    color: #4A4A4A;
    letter-spacing: 0;
    line-height: 26px;
}

.FontCity{
    font-size: 12px;
    color: #4A4A4A;
    line-height: 18px;
}

.TicketStops {
    font-size: 10px;
    color: #8B9497;
    text-align: center;
    line-height: 12.1px;
}

.FontLeft {
    text-align: left;
}

@media (max-width: 768px) {
    .FontLeft {
        text-align: center;
    }
}

.lds-roller {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}
.lds-roller div {
    -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    -webkit-transform-origin: 32px 32px;
            transform-origin: 32px 32px;
}
.lds-roller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #cef;
    margin: -3px 0 0 -3px;
}
.lds-roller div:nth-child(1) {
    -webkit-animation-delay: -0.036s;
            animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
    top: 50px;
    left: 50px;
}
.lds-roller div:nth-child(2) {
    -webkit-animation-delay: -0.072s;
            animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
    top: 54px;
    left: 45px;
}
.lds-roller div:nth-child(3) {
    -webkit-animation-delay: -0.108s;
            animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
    top: 57px;
    left: 39px;
}
.lds-roller div:nth-child(4) {
    -webkit-animation-delay: -0.144s;
            animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
    top: 58px;
    left: 32px;
}
.lds-roller div:nth-child(5) {
    -webkit-animation-delay: -0.18s;
            animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
    top: 57px;
    left: 25px;
}
.lds-roller div:nth-child(6) {
    -webkit-animation-delay: -0.216s;
            animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
    top: 54px;
    left: 19px;
}
.lds-roller div:nth-child(7) {
    -webkit-animation-delay: -0.252s;
            animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
    top: 50px;
    left: 14px;
}
.lds-roller div:nth-child(8) {
    -webkit-animation-delay: -0.288s;
            animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
    top: 45px;
    left: 10px;
}
@-webkit-keyframes lds-roller {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes lds-roller {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

